import React from "react"
import FolderImage from './img/MindSpaceRGB-11.svg'
import FolderImageHover from './img/MindSpaceRGB neu-18.svg'
import Dum from "./sketch"
import Modal2 from "./Modal";

class Ordner extends React.Component {
    constructor(props){
      super(props);
        this.state = {
            modals: [
                { id: 1, name: "Polycon", className: "popup2", show: true, isActive: false, content: <Dum/>, image: FolderImage,  hoverImage: FolderImageHover},
                { id: 2, name: "Ordner", className: "popup2", show: true, isActive: false, content: <Dum/>, image: FolderImage, hoverImage: FolderImageHover },
             
              ],
        }
        this.state.modals.forEach(function (modal) {
          modal.showModal = false;
        });
        console.log(this.state.modals)
      }
    
  getModal = data => {
    let modal;
    this.state.modals.forEach(function (modal) {
      modal.isActive = false;
    });
     let modals = [...this.state.modals];
    if (data.id == 100){
      modal = modals[1]
    } else {
      modal = {...modals[data.id-1]};
    }
    modal.showModal = true;
    modal.isActive = true;
    modals[data.id-1] = modal;
    this.setState({modals}, () => { console.log(this.state.modals);});
   
  };
  
  hideModal = data => {
    let modals = [...this.state.modals];
    let modal = {...modals[data.id-1]};
    modal.showModal = false;
    modal.isActive = false;
    modals[data.id-1] = modal;
    this.setState({modals});
  };
  
  getActive = data => {
    this.state.modals.forEach(function (modal) {
      modal.isActive = false;
    });
    let modals = [...this.state.modals];
    let modal = {...modals[data.id-1]};
    
    modals[data.id-1] = modal;
    modal.isActive = true;
    this.setState({modals}, () => { console.log(modal + " ist aktiv");});
   
  };
  
  
  
  render() {
    
   
    return (
  
  
    
     <div className="folder">
       
        { this.state.modals.map((data, key) => (
         
    
          <div key={data.id} className="wrapper">
              <div onClick={() => this.getModal(data)}>
              <img src={data.image}
                    onMouseEnter={e => (e.currentTarget.src = data.hoverImage)}
                    onMouseOut={e => (e.currentTarget.src = data.image)}
                  />
                  <p>{data.name}</p></div>
          </div> 
        ))}
  
      {this.state.modals.map((data) => 
          <Modal2
            show={this.state.modals[data.id-1].showModal}
            onHide={() => this.hideModal(data)}
            name={this.state.modals[data.id-1].name}
            content={this.state.modals[data.id-1].content}
            image={this.state.modals[data.id-1].image}
            hoverImage={this.state.modals[data.id-1].hoverImage}
            modalClass={this.state.modals[data.id-1].className}
            isActive={this.state.modals[data.id-1].isActive}
            onClick={() => this.getActive(data)}
          />
       )}
      </div>
  
  
  
  )}}

  export default Ordner